import React, { useEffect, useState } from 'react'

export default function History() {

  const [History, setHistory] = useState([]);

  useEffect(() => {
    fetch('/gethistory', {
      method: "get",
      headers: {
        Authorization: `Bearer ${localStorage.getItem('jwt')}`,
      },
    })
      .then((res) => res.json())
      .then((data) => {
        setHistory(data);
        console.log(data);
      })
      .catch((error) => console.log(error));
  }, []);
  return (
    <div className='bg-gradient-to-tl from-green-800  to-black min-h-screen max-h-[775px] w-[400px] overflow-auto m-2'>{console.log(History)}
      <h1 className='text-white text-xl'>History</h1>
      <br/>
      <ul className=''>
        {History.map((item, index) => {
          const [request1, response] = item.split("----------");
          return (
            <div className='overflow-auto flex justify-between'>
            <li className='m-2 text-white text-base' key={index}>
              {`${request1} `}
            </li>
            <li className='m-2 text-white text-base' key={index}>{`${response}`}</li>
            </div>
          );
        })}
      </ul>
    </div>
  )
}
