import React from "react";
import { useEffect } from "react";
// import PropTypes from "prop-types";
// import Spinner from "./Spinner";
import BlockPhone from "../images/BlockPhone.png";
import BlockMail from "../images/BlockMail.png";
import ResultCard from "./ResultCard";
import { useState } from "react";
import axios from "axios";
import { validate, getAddressInfo } from "bitcoin-address-validation";
import { useAuth0 } from "@auth0/auth0-react";

function SearchCard(props) {
  const { user, isAuthenticated, isLoading } = useAuth0();
  const name = [
    "",
    "Phone No. Validation",
    "Email Validation",
    "SMS Validation",
    "URL Validation",
    "UPI ID Validation",
    "BTC-wallet Validation",
  ];
  const [resultFinal, setresultFinal] = useState(0);
  const [spamData, setSpamData] = useState("");
  const [tempData, setTempData] = useState("");
  const [text, setText] = useState("");
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState(false);
  const [email, setemail] = useState(0);

  const handleOnChange = (event) => {
    console.log("On Change");
    setText(event.target.value);
  };

  //-----------------------------------------------
  //Phone No.
  const makeAction = async () => {
    //this is for phone validataion
    // e.preventDefault();
    // setResult(false);
    console.log(spamData);
    axios
      .get(`/api_no?phone=${spamData}`)
      .then((response) => {
        console.log("response - " + response.data.message);
        setTempData(response.data.message);
        console.log(response);
        setresultFinal(parseInt(response.data.message));
        console.log(resultFinal);
        setResult(true);
        const historyItem = `${spamData}----------${response.data.message}`;
        console.log("history - " + historyItem);
        if (isAuthenticated) saveHistory(historyItem);
      })
      .catch((error) => {
        console.error("error in makeAction - " + error);
      });
  };
  //---------------------------------------------
  //Email
  const check_email = () => {
    // e.preventDefault();
    axios
      .post("/verify_email", { email: spamData })
      .then((res) => {
        setTempData(res.data);
        console.log("res-- " + JSON.stringify(res));
        console.log("res data  " + res.data);
        const arr = res.data.split(" ");
        // console.log(arr[0]);
        // console.log(arr[1]);
        if (arr[0] === "true" && parseInt(arr[1]) > 80) {
          setresultFinal(0);
        } else if(arr[0] == "true" && parseInt(arr[1]) <= 80){
          setresultFinal(1);
        }
        else if(arr[0] == "false"){
          setresultFinal(0);
        }
        setResult(true);
        const historyItem = `${spamData}----------${res.data}`;
        console.log("history - " + historyItem);
        if (isAuthenticated) saveHistory(historyItem);
      })
      .catch((err) => console.error(err));
  };
  //----------------------------------------------------
  //URL
  const check_url = () => {
    // e.preventDefault();
    axios
      .post("/verify_url", { url: spamData })
      .then((res) => {
        setTempData(res.data);
        console.log("res-- " + JSON.stringify(res));
        console.log("res data unsafe - " + res.data.unsafe);
        console.log("res data suspicious - " + res.data.suspicious);
        console.log("res data risk_score - " + res.data.risk_score);
        if (res.data.unsafe === "true" || res.data.suspicious === "true") {
          setresultFinal(0);
        } else if(res.data.unsafe === "false" && res.data.suspicious === "false"){
           setresultFinal(1)
        };
        setResult(true);
        const historyItem = `${spamData}----------${res.data.unsafe} ${res.data.suspicious} ${res.data.risk_score}`;
        console.log("history - " + historyItem);
        if (isAuthenticated) saveHistory(historyItem);
      })
      .catch((err) => console.error(err));
  };
  //-------------------------------------------------------
  //Bitcoin
  const btc_val = () => {
    let ans = validate(spamData);
    if (ans) {
      setresultFinal(1);
    } else {
      setresultFinal(0);
    }
    setResult(true);
  };
  //---------------------------------------------------------
  //UPI
  const check_upi = () => {
    // e.preventDefault();

    const regex = /\w{2,11}@\w{2,4}/;
    // let regex = new RegExp(/^[a-zA-Z0-9.-]{2, 256}@[a-zA-Z][a-zA-Z]{2, 64}$/);
    if (regex.test(spamData)) {
      setresultFinal(1);
      console.log("valid");
      setTempData("valid");
      const historyItem = `${spamData}----------valid`;
      console.log("history - " + historyItem);
      if (isAuthenticated) saveHistory(historyItem);
    } else {
      setresultFinal(0);
      console.log("invalid");
      setTempData("invalid");
      const historyItem = `${spamData}----------invalid`;
      console.log("history - " + historyItem);
      if (isAuthenticated) saveHistory(historyItem);
    }
    setResult(true);
  };
  //--------------------------------------------------------
  //SMS
  const check_sms = () => {
    // e.preventDefault();

    let val = spamData;
    const words = val.split(" ");
    const regexPattern = /^\d+(\d{3})*\/( Rs day| Rs month)$/;

    let isValid = true;
    for (let i = 0; i < words.length; i++) {
      if (
        words[i] === "lottery" ||
        words[i] === "Lottery" ||
        regexPattern.test(words[i])
      ) {
        isValid = false;
        break;
      }
    }

    if (isValid) {
      console.log("Valid SMS");
      setTempData("Valid SMS");
      const historyItem = `SMS----------Valid SMS`;
      console.log("history - " + historyItem);
      setresultFinal(1);
      if (isAuthenticated) saveHistory(historyItem);
    } else {
      setresultFinal(0);
      console.log("InvalidSMS");
      setTempData("InvalidSMS");
      const historyItem = `SMS----------InvalidSMS`;
      console.log("history - " + historyItem);
      if (isAuthenticated) saveHistory(historyItem);
    }
    setResult(true);
  };
  //-------------------------------------------------------
  //SignIn useEffect
  useEffect(() => {
    if (isAuthenticated) {
      console.log("user- name " + user.name);
      const sname = user.name;
      const semail = user.email;
      axios
        .post("/signin", {
          email: semail,
          name: sname,
        })
        .then(
          (response) => {
            console.log(response.data);
            localStorage.setItem("jwt", response.data.token);
            localStorage.setItem("user", JSON.stringify(response.data.user));
            // notifyB("Your profile")
          },
          (error) => {
            console.log(error);
            // notifyA(error)
          }
        );
    }
    console.log("here--");
  }, []);
  //-------------------------------------------------------
  //History Save
  const saveHistory = (historyItem) => {
    fetch("/history", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + localStorage.getItem("jwt"),
      },
      body: JSON.stringify({
        history: historyItem,
      }),
    })
      .then((res) => res.json())
      .then((res) => console.log(res))
      .catch((err) => console.log(err));
  };

  //--------------------------------------------------

    // const ClickAction = (e) =>{
    //   e.preventDefault();
    //   SubmitEvent()
    // }


  //-------------------------------------------------
  const SubmitEvent = async () => {
    setSpamData(text);

    if (props.title == 1) {
      makeAction();
    } else if (props.title == 2) {
      check_email();
    } else if (props.title == 3) {
      check_sms();
    } else if (props.title == 4) {
      check_url();
    } else if (props.title == 5) {
      check_upi();
    } else if (props.title == 6) {
      btc_val();
    }
    // console.log("====================================");
    // console.log(parsedData);
    // console.log("====================================");
    // }
  };

  return (
    <div className="w-2/4">
      <div className="flex justify-center  w-full">
        <div className="flex justify-center m-5  w-full">
          <div className="flex flex-col rounded-lg dark:bg-grey-900 md:max-w-xl md:flex-row">
            <div className="flex-wrap justify-between inline-block">
              <div>
                <img
                  className="h-96 mt-2 mx-auto rounded-t-lg object-cover md:h-auto md:w-48 md:rounded-none md:rounded-l-lg"
                  src={BlockMail}
                  alt=""
                />
              </div>
              <div className="flex flex-col justify-start p-6">
                <p className="mb-2 text-xl font-medium text-white dark:text-neutral-50">
                  {name[props.title]}
                </p>
              </div>
              <div className="w-">
                <p className="mb-1 text-base text-white dark:text-neutral-200">
                  Enter the detail
                </p>
                <form>
                  <label
                    for="search"
                    className="mb-2 text-sm font-medium text-gray-900 sr-only dark:text-white"
                  >
                    Search
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none"></div>
                    <input
                      value={text}
                      onChange={handleOnChange}
                      id="search"
                      className="block w-full p-4 pl-10 text-sm text-white border border-white rounded-lg bg-gray-900 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-900 dark:border-white dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
                      placeholder={"Search"}
                      required
                    />

                    <button
                      type="submit"
                      className="text-white m-1 right-2.5 bottom-2.5 bg-green-900 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-4 py-2 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-white"
                      onClick={(e)=>{e.preventDefault();SubmitEvent()}}
                    >
                      Search
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div>{result && <ResultCard parameters={resultFinal} />}</div>
    </div>
  );
}

SearchCard.propTypes = {};

export default SearchCard;

