import React, { useState } from "react";
import SearchCard from "./SearchCard.js";
// import Spinner from "./Spinner"
import ResultCard from "./ResultCard.js";

function verification() {
  const [isEmailVisible, setIsEmailVisible] = useState(false);
  const [isPhoneVisible, setIsPhoneVisible] = useState(true);
  const [isUrlVisible, setIsUrlVisible] = useState(false);
  const [isCryptoVisible, setIsCryptoVisible] = useState(false);
  const [isUPIVisible, setIsUPIVisible] = useState(false);
  const [isSMSVisible, setIsSMSVisible] = useState(false);

  const toggleEmailVisibility = () => {
    setIsEmailVisible(!isEmailVisible);
    setIsPhoneVisible(false);
    setIsUrlVisible(false);
    setIsUPIVisible(false);
    setIsCryptoVisible(false);
    setIsSMSVisible(false);
  };

  const togglePhoneVisibility = () => {
    setIsEmailVisible(false);
    setIsPhoneVisible(!isPhoneVisible);
    setIsUrlVisible(false);
    setIsUPIVisible(false);
    setIsCryptoVisible(false);
    setIsSMSVisible(false);
  };

  const toggleUrlVisibility = () => {
    setIsEmailVisible(false);
    setIsPhoneVisible(false);
    setIsUPIVisible(false);
    setIsUrlVisible(!isUrlVisible);
    setIsCryptoVisible(false);
    setIsSMSVisible(false);
  };

  const toggleCryptoVisibility = () => {
    setIsEmailVisible(false);
    setIsPhoneVisible(false);
    setIsUrlVisible(false);
    setIsUPIVisible(false);
    setIsCryptoVisible(!isCryptoVisible);
    setIsSMSVisible(false);
  };
  const toggleUPIVisibility = () => {
    setIsEmailVisible(false);
    setIsPhoneVisible(false);
    setIsUrlVisible(false);
    setIsCryptoVisible(false);
    setIsUPIVisible(!isUPIVisible);
    setIsSMSVisible(false);
  };
  const toggleSMSVisibility = () => {
    setIsEmailVisible(false);
    setIsPhoneVisible(false);
    setIsUrlVisible(false);
    setIsCryptoVisible(false);
    setIsUPIVisible(false);
    setIsSMSVisible(!isSMSVisible);
  };

  return (
    <div className="absolute bg-gray-900 h-full w-full">
      <div className="">
        <div className=" rounded-t-lg my-5">
          <header className="rounded-lg">
            <nav className="w-full">
              <div className="h-16">
                <ul className="grid grid-cols-3 items-center">
                  <li className="mx-2 my-2">
                    <div
                      href="#"
                      className="text-white flex justify-center hover:bg-emerald-400 hover:text-black px-3 py-3 rounded-md text-sm font-medium "
                      onClick={togglePhoneVisibility}
                    >
                      Phone
                    </div>
                  </li>
                  <li className="mx-2 my-2">
                    <div
                      href="#"
                      className="text-white flex justify-center hover:bg-emerald-400 hover:text-black px-3 py-3 rounded-md text-sm font-medium"
                      onClick={toggleEmailVisibility}
                    >
                      Email
                    </div>
                  </li>
                  <li className="mx-2 my-2">
                    <div
                      href="#"
                      className="text-white flex justify-center hover:bg-emerald-400 hover:text-black px-3 py-3 rounded-md text-sm font-medium"
                      onClick={toggleSMSVisibility}
                    >
                      SMS
                    </div>
                  </li>
                  <li className="mx-2 my-2">
                    <div
                      href="#"
                      className="text-white flex justify-center hover:bg-emerald-400 hover:text-black px-3 py-3 rounded-md text-sm font-medium"
                      onClick={toggleUrlVisibility}
                    >
                      URL
                    </div>
                  </li>
                  <li className="mx-2 my-2">
                    <div
                      href="#"
                      className="text-white flex justify-center hover:bg-emerald-500 hover:text-black px-3 py-3 rounded-md text-sm font-medium"
                      onClick={toggleUPIVisibility}
                    >
                      UPI
                    </div>
                  </li>
                  <li className="mx-2 my-2" xxxxxxx>
                    <div
                      href="#"
                      className="text-white flex justify-center hover:bg-emerald-400 hover:text-black px-3 py-3 rounded-md text-sm font-medium"
                      onClick={toggleCryptoVisibility}
                    >
                      Crypto
                    </div>
                  </li>
                </ul>
              </div>
            </nav>
          </header>
        </div>
      </div>
      <div className="flex justify-center mt-10">
        <div className="">
          {isPhoneVisible  && <SearchCard title={"1"} />}
          {isEmailVisible  && <SearchCard title={"2"} />}
          {isSMSVisible    && <SearchCard title={"3"} />}
          {isUrlVisible    && <SearchCard title={"4"} />}
          {isUPIVisible    && <SearchCard title={"5"} />}
          {isCryptoVisible && <SearchCard title={"6"} />}
        </div>
      </div>
    </div>
  );
}

export default verification;
