import React, {useEffect, useState } from 'react'

export default function CommentCard() {
  const [value, setValue] = useState([]);
  const [data, setData] = useState([]);

// this section for fetch all data
  useEffect(() => {

    //Fetch all posts
    fetch("/allposts", {
      method:"get",
      headers: {
        "Authorization": "Bearer " + localStorage.getItem("jwt")
      },
    }).then(res => res.json())
      .then(res => setValue(res))
      .catch(err => console.log(err))
  }, [])


  return (
    <>
      <div className='bg-black'>
        <p className='mx-1 text-white text-2xl'>What are people saying...</p>
        <br/>
        {value.map((post)=>{
          const { title, comments } = post;
          return(
            <div className='overflow-auto'>
              {console.log(post)}
              {comments.map((innerview)=>{
                return (
                  <div className='overflow-clip'>
                  <li className='text-white text-lg'>Title: {title}</li>
                  <li  className='text-white text-base'>Comment: {innerview.comment}</li>
                  <li  className='text-white text-base'>Poster: {innerview.postedBy.name}</li>
                  </div>
                )
              })}

              
              <br />
              <br />
            </div>
              
          )
        })}
      </div>
    </>
  )
}
