import React from "react";
import PropTypes from "prop-types";
import Warning  from "../images/Warning.png";
import Success from "../images/Success.png"

function ResultCard(props) {
  return (
    <div className="">
      <div
        className={`flex items-center bg-black border border-${props.parameters == 0 ? "red" : "emerald"}-300 rounded-lg shadow hover:bg-gray-100 dark:border-gray-700 dark:bg-gray-800 dark:hover:bg-gray-700 flex justify-center mx-5`}
      >
        <img
          className="object-cover rounded-t-lg h-20 "
          src={(props.parameters == 0) ? Warning : Success}
          alt=""
        />
        <div className="flex flex-col justify-between p-4 leading-normal">
          <h5 className="mb-2 text-2xl font-bold tracking-tight text-white dark:text-white">
            {(props.parameters == 0) ? "This looks suspicious. Proceed with caution...." : "Looks good. Go right ahead...."}
          </h5>
          <p className="mb-3 font-normal text-gray-700 dark:text-gray-400">
            
          </p>
        </div>
      </div>
    </div>
  );
}

ResultCard.propTypes = {};

export default ResultCard;