import { useAuth0 } from "@auth0/auth0-react";
import React from "react";

const LoginButton = () => {
  const { loginWithRedirect } = useAuth0();
  return <button className="cursor-pointer rounded-full [border:none] p-0 bg-turquoise-100 absolute h-[45%] w-[9.1%] top-[28.05%] right-[8.33%] bottom-[26.95%] left-[82.57%]" onClick={() => loginWithRedirect()}>Sign Up</button>;
};

export default LoginButton;