import React, { useEffect, useState } from 'react'

export default function History() {

  const [History, setHistory] = useState([]);

  useEffect(() => {
    fetch('/gethistory', {
      method: "get",
      headers: {
        Authorization: `Bearer ${localStorage.getItem('jwt')}`,
      },
    })
      .then((res) => res.json())
      .then((data) => {
        setHistory(data);
        console.log(data);
      })
      .catch((error) => console.log(error));
  }, []);
  return (
    <div className='bg-black ml-2 max-h-[700px] overflow-y-auto overflow-x-clip'>{console.log(History)}
      <h1 className='text-white text-4xl'>History</h1>
      <br/>
      <ul className=''>
        {History.map((item, index) => {
          const [request1, response] = item.split("----------");
          return (
            <div className='flex justify-between border-y-2 m-2 bg-slate-600'>
            <li className='mb-1 mx-1 text-white text-2xl w-[3/4] text-clip' key={index}>
              {`${request1} `}
            </li>
            <li className='mb-2 mx-1 text-white text-2xl' key={index}>{`${response}`}</li>
            </div>
          );
        })}
      </ul>
    </div>
  )
}
