import React, { useState } from 'react';
import { RiMenu3Line, RiCloseLine } from 'react-icons/ri';
import Logo from './Logo.svg';
import { useAuth0 } from "@auth0/auth0-react";
import './Navbar.css';
import Login from '../Login.jsx'
import Logout from '../Logout.jsx'
import { NavLink } from 'react-router-dom'

const Navbar = () => {
    const [toggleMenu, setToggleMenu] = useState(false);
    const { user, isAuthenticated } = useAuth0();

    return (
        <div className="gpt3__navbar bg-gray-900">
            <div className="gpt3__navbar-links">
                <div className="gpt3__navbar-links_logo">
                    <img src={Logo} />
                </div>
                <div className="gpt3__navbar-links_container">
                    {isAuthenticated ? (
                        <>
                            <p><NavLink exact to={'/'}>Home</NavLink></p>
                            <p><NavLink exact to={'/profile'}>Profile</NavLink></p>
                            <p><NavLink exact to={'/review'}>Feedback</NavLink></p>
                            <p><NavLink exact to={'/history'}>History</NavLink></p>
                            <p><NavLink exact to={'/verify'}>Verification</NavLink></p>
                            <p><NavLink exact to={'/about'}>About Us</NavLink></p>
                            <p><NavLink exact to={'/team'}>Our Team</NavLink></p>
                        </>
                    ) : 
                        (
                            <>

                                <p><NavLink exact to={'/'}>Home</NavLink></p>
                                <p><NavLink exact to={'/verify'}>Verification</NavLink></p>
                                <p><NavLink exact to={'/about'}>About Us</NavLink></p>
                                <p><NavLink exact to={'/team'}>Our Team</NavLink></p>
                            </>
                        )}

                </div>
            </div>
            <div className="gpt3__navbar-sign">
                {isAuthenticated ? (
                    <p><Logout /></p>
                ) : <p><Login /></p>}
            </div>
            <div className="gpt3__navbar-menu">
                {toggleMenu
                    ? <RiCloseLine color="#fff" size={27} onClick={() => setToggleMenu(false)} />
                    : <RiMenu3Line color="#fff" size={27} onClick={() => setToggleMenu(true)} />}
                {toggleMenu && (
                    <div className="gpt3__navbar-menu_container scale-up-center bg-black z-40">
                        <div className="gpt3__navbar-menu_container-links">
                            {isAuthenticated ? (
                                <>
                                    <p><NavLink exact to={'/'}>Home</NavLink></p>
                                    <p><NavLink exact to={'/profile'}>Profile</NavLink></p>
                                    <p><NavLink exact to={'/verify'}>Verification</NavLink></p>
                                    <p><NavLink exact to={'/review'}>Feedback</NavLink></p>
                                    <p><NavLink exact to={'/history'}>History</NavLink></p>
                                    <p><NavLink exact to={'/about'}>About US</NavLink></p>
                                    <p><NavLink exact to={'/team'}>Our Team</NavLink></p>
                                </>
                            ) :
                                (
                                    <>
                                        <p><NavLink exact to={'/'}>Home</NavLink></p>
                                        <p><NavLink exact to={'/verify'}>Verification</NavLink></p>
                                        <p><NavLink exact to={'/about'}>About Us</NavLink></p>
                                        <p><NavLink exact to={'/team'}>Our Team</NavLink></p>
                                    </>
                                )}
                        </div>
                        <div className="gpt3__navbar-menu_container-links-sign">
                            {isAuthenticated ? (
                                <p><Logout /></p>
                            ) : <p><Login /></p>}
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};

export default Navbar;