import { useAuth0 } from "@auth0/auth0-react";
import React from "react";

const LogoutButton = () => {
  const { logout } = useAuth0();

  return (
    <button className="cursor-pointer rounded-full [border:none] p-0 bg-turquoise-100 absolute h-[45%] w-[9.1%] top-[28.05%] right-[8.33%] bottom-[26.95%] left-[82.57%]" onClick={() => {logout({ logoutParams: { returnTo: window.location.origin } }); localStorage.clear()}}>
      Log Out
    </button>
  );
};

export default LogoutButton;